import React, { Component } from 'react';

// css
import classes from './add-item.module.scss';

const {formBlock, buttonSeveBlock, formText, formInput, buttonSeve} = classes;

export default class AddItem extends Component {

  state = {
      text: ''
  }

  onTextChange = (e) => {
      this.setState({
          text: e.target.value
      });
  }

  onSubmit = (e) => {
      e.preventDefault();
      this.props.onAddItem(this.state.text);
      this.setState({
          text: e.target.value
      });
  }

  render() {
      // const {onAddItem} = this.props;
      // const text = this.state.text;
      const task = 'Task';
      const saveItem = 'Save Item';
      return (
        <form onSubmit={this.onSubmit} className={formBlock}>
          <div className={buttonSeveBlock}>
            <span className={formText}>{ task }</span>
            <input 
                onChange={this.onTextChange}
                className={formInput} 
                type="text" 
                placeholder="What do you need to do?" 
                value={this.state.text}
            />
          </div>
          <button type="submit" className={buttonSeve}>{saveItem}</button>
        </form>
      );
  }

}
