import React, { Component } from 'react';

// components import
import SearchPanel from './../search-panel';
import TodoList from './../todo-list';
import AddItem from './../add-item';
import FilterPanel from './../filter-panel';

// css
import classes from './app.module.scss';

const {app} = classes;

export default class App extends Component {

    state = {
        todoList: [
          {id: 1, text: 'Wake up', checked: true},
          {id: 2, text: 'Eat breakfast', checked: false},
          {id: 3, text: 'Go to work', checked: false}
      ]
    };

    itemId = 10;

    delItem = (id) => {

        this.setState(({todoList})=>{
            const idx = todoList.findIndex((item)=>id === item.id );
            const newTodoList = [
                ...todoList.slice(0, idx),
                ...todoList.slice(idx + 1)
            ];

            return {
              todoList: newTodoList
            }
        });
    }

    addItem = (text) => {
      console.log(text);
      const newTodoItem = {id: this.itemId++ , text: text, checked: false};

      this.setState(({todoList})=>{
          const newTodoList = [
            ...todoList,
            newTodoItem
          ];
          
          return {
              todoList: newTodoList
          }
      });
    }

    render() {
        return (
            <div className={app}>
                <SearchPanel />
                <FilterPanel />
                <TodoList 
                    todos={this.state.todoList}
                    onDelItem={this.delItem}
                />
                <AddItem onAddItem={this.addItem}/>
            </div>
        );
    }

}


