import React, { Component } from 'react';

// css
import classes from './filter-panel.module.scss';


export default class FilterPanel extends Component {
    state = {
        all: true,
        completed: false,
        important: false
    }

    filterAll = () => {
      this.setState( ({all}) => {
          return {
              all: !all
          }
      });
    }

  

    render() {
        const {all} = this.state;
        const {filterPanel, filterPanelAll} = classes;
        let itemClasses = filterPanel;
        
        if (all) {
          itemClasses += ` ${filterPanelAll}`;
        }


        return (
            <ul className={filterPanel}>
              <li onClick={this.filterAll} className={itemClasses}>All</li>
              <li>Completed</li> 
              <li>important</li>
            </ul>
          );
    }
 
}
