import React, { Component } from 'react';

// import TodoSubmit from './../todo-submit';

// css
import classes from './todo-item.module.scss';

export default class TodoItem extends Component {
    state = {
        done: false,
        important: false,
        del: false
    }

    itemDone = () => {
        this.setState( ({done}) => {
            return {
                done: !done
            }
        });
    }

    setImportant = () => {
        this.setState( ({important}) => {
            return {
                important: !important
            }
        });
    }

    delItem = () => {
      
    }

    render () {
        const {text, onDelItem} = this.props;
        const {done, important} = this.state;
        // classes
        const {todoItem, todoItemDone, itemText, todoItemImportant, todoSubmitСlose, todoSubmitOk} = classes;
        let itemClasses = todoItem;

        if (done) {
            itemClasses += ` ${todoItemDone}`;
        }

        if (important) {
            itemClasses += ` ${todoItemImportant}`;
        }

        // if (del) {
        //     itemClasses += ` ${todoItemDel}`;
        // }

        return (
            <li className={itemClasses}>
                <span onClick={this.itemDone} className={itemText}>{text}</span>
                <div>
                  <button onClick={this.setImportant} className={todoSubmitСlose}>✓</button>
                  <button onClick={onDelItem} className={todoSubmitOk}>X</button>
                </div>
            </li>
        );
    }
}
  


