import React from 'react';

// css
import classes from './search-panel.module.scss';

const {searchPanel, todoTitle, todoSearch, todoInput, todoSubmit} = classes;

const SearchPanel = () => {
    const title = 'To do:';
    return (
      <div className={searchPanel}>
        <h1 className={todoTitle}>{ title }</h1>
        <div className={todoSearch}>
          <input className={todoInput} type="text" placeholder="search..." />
          <button className={todoSubmit}>Search</button>
        </div>
      </div>   
    );
}

export default SearchPanel;